import React, { Component } from 'react';
import { Redirect, Link } from 'react-router-dom';
import { connect } from 'react-redux';

 class Homepage extends Component {
    render() {
        const { auth } = this.props
        if (auth.uid) return <Redirect to = '/dashboard' />
        return (
            <div>
                <header id="home">
                    <div className=" welcome-text valign-wrapper">
                        <div className=" center-align welcome">
                            <h3><b>STUDY GURU</b> APP</h3> 
                            <p className="text-darken-5">
                                Join Millions of peolple from around the world today, and study together from your device anytime, anywhere.
                                Connect with friends and class mates and take the studying digital.
                            </p>               
                        </div>
                    </div>
                </header>

                <section id="about" className="valign-wrapper">
                    <div className="container black-text text-lighten-4">
                        <div className="row">
                            <div className="col s12 m12 pull-12">
                                <div className="right-align ">
                                    <p className ="pink-text"> WHO IS STUDY GURU?</p>
                                    <h4 style= {{fontWeight: "bold"}} > A BIT ABOUT US</h4>
                                    <span style= {{fontWeight: 600}}>We are the future means of  study meeting people from diiferet art of the world</span><br/>
                                    <p>
                                        Create Your cards containg your questions getting access to your cloud <br/> 
                                        platform storing them and sharing with your others from around the world <br/>
                                        <br/>Refer back to your cards <b>anytime, anywhere</b> and View other cards from fellow students. <br/> 
                                        
                                    </p>
                                    <Link to = '/signin'>
                                        <button className="btn waves-effect pink lighten-1" type= "button" style = {{borderRadius : '50px'}} >Get Started</button>
                                    </Link>
                                    </div>
                            </div>
                        </div>                
                    </div>
                </section>

                <div className="center" style= {{marginTop: '80px'}}>
                    <p className= 'red-text'>WHAT DO PEOPLE SAY</p>
                    <h4 style={{fontWeight: 'bold'}}>OUR SATISFIED CLIENT</h4>
                    <p style={{margin: '20px'}}>
                        Words from others can serve as a means of showing how they feel abut our app. <br /> Just a few of them could be a good testimony 
                    </p>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col s12 l12">
                            <div className= "carousel carousel-slider center" data-indicators="true">
                                <div className="carousel-item black-text" href="#one">
                                    <h2>John Snow</h2>
                                    <p>I think the app works well. it helps me in time of need when i feel like studying.
                                        <br />I recommend you using this app, it is nice. 
                                    </p>
                                </div>
                                <div className="carousel-item black-text" href="#two">
                                    <h2>Arya Stark</h2>
                                    <p>I think the app works well. it helps me in time of need when i feel like studying.
                                        <br />I recommend you using this app, it is nice. 
                                    </p>
                                </div>
                                <div className="carousel-item black-text" href="#three">
                                    <h2>Tyrion Lannister</h2>
                                    <p>I think the app works well. it helps me in time of need when i feel like studying.
                                        <br />I recommend you using this app, it is nice. 
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                <footer className="black white-text center">
                    <div style={{padding: '30px 0 20px 0'}}>
                        <h3>Study Guru</h3>
                        <ul>
                            <li><i className="fa fa-facebook"></i></li>
                            <li><i className="fa fa-instagram"></i></li>
                            <li><i className="fa fa-twitter"></i></li>
                            <li><i className="fa fa-youtube"></i></li>
                        </ul>
                    </div>
                    <div className= "grey darken-4" style= {{padding: '30px 0'}}>
                        &copy; 2019 Study Guru. All Rights Reserved
                    </div>
                </footer>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.firebase.auth
    }
}

export default connect(mapStateToProps)(Homepage)